<script>
import Title from "@/common/title";
import { Toast } from 'vant';	
import AeraInfo from "../../common/area.js";
export default {
  components: {
    Title

  },
  data() {
    return {
		areaList: AeraInfo,
		active: 1,
		id:'',
		news:'',
		file:'',
		ying:'',
		 showPicker:false,
		 show:false,
		reg: {
			store_name:'',
			city_code:'',
			phone:'',
			password:'',
			repassword:'',
			cinemas:''
			         
		      },
    
    };
  },mounted() {
	  
	if(this.$route.query.id){
		this.reg.id = this.$route.query.id
		this.sandCate()
	}
	  
  	
	this.sandMate()
	this.sandCase()
  },methods:{
	   onConfirm(value) {
		  
	  
		
		   this.reg.city_code=value[1].code
		   this.ying=value[1].name
	       this.showPicker = false;
	      },
	  submitForm() {
	  				
	  		
	          
	          this.$axios({
	            method: "post",
	            url: "stores/register",
	            data: this.reg,  
	          }).then((res) => {
	          	let data=res.data;
	          			
	          	if(data.code==200){
	  						
	  			  Toast(data.message);
				  this.$router.back()
	    
	          		
	          	}else{  
	  						  Toast(data.message);
	  						      
	  					}
	           
	          });
	  				 
	  				 
	         
	       
	       }
  }
};
</script>
