							{{it.citys}}
						</div>
						
					</div>
				<van-row class="can2" gutter='20' >
					<div class="lt">
						<div class="tit">{{it.movie_name}} <span>x{{it.ticket_count}}</span></div>
						<p> {{it.show_time_txt}}</p>
						<p>{{it.cinemas}}</p>
						<p>{{it.halls}}</p>
						<p>{{it.seat_names}}</p>
						<p class="seat" v-if="it.accept_seats==1"></p>
						<p class="seat" v-if="it.accept_seats==0"></p>
					</div>
