					<p v-if="id>1">{{it.offer_amount}}</p>
					<p class="inter" v-if="id>1">{{it.market_price}}</p>
				</van-col>
				
				<van-col span="11" class="rit">
					<div class="ig" v-if="id==1" @click="wind(it)"></div>
					<div class="h-button" v-if="id==1">
						<router-link :to="'details?id='+it.order_no"></router-link>
						
						</div>
						
