<script>
import Title from "@/common/title";
import { Toast,Dialog } from 'vant';
export default {
  components: {
    Title
  },
  data() {
    return {
      timedai:null,
      dainum:null,
      number:null,
      type:"1",
		title:'',
		id:1,
		keywords:'',
		images:[],
		info:'',
		user:'',
		page:1,
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:''

    };
  },  mounted() {

	this.sandRed()
	this.sandMate()
  this.getdainum()
    },

  methods: {
    getdainum(){
      this.$axios.get('stores/order_list',{params:{
          type:3,keywords:''
        }}).then((res)=>{
        console.log(res);
        let data=res.data;
        if(data.code==200){
          if(data.data.list.data.length!=0){
            this.dainum = data.data.list.data.length
            this.timedai=data.data.list.data[0].expire_time
            console.log(this.timedai)
            let a = new Date().getTime()
            this.timedai = this.timedai*1000-a
            console.log(this.timedai)
          }

        }
      })
    },
    baojia(it){
      if(it.number == null){

        Toast('');
        return
      }


      this.$axios({
        method: "post",
        url: "stores/create_offer",
        data: {
          price:it.number,
          order_no:it.order_no
        },
      }).then((res) => {
        let data=res.data;

        if(data.code==200){

          this.sandMate()
          Toast(data.message);


        }else{
          Toast(data.message);

        }

      });
    },
    selecttype(type) {
      if(type=="2"){
        window.location.href = "https://dyp.ylhwlkj.com";
        // window.location.href = "https://xcx.ylhwlkj.com";
      }



    },
    nopop(){
      this.$axios({
        method: "post",
        url: "stores/nopop",
        data: {},
      }).then((res)=>{
        Toast('');
      })
    },
			ctime(id){
			
				  
					
								 var now= Math.round(new Date().getTime()/1000).toString();
								
				              
				                 var leftTime =  id-now ;
				               
								 
				                 if (leftTime >= 0) {
									 
				                   let d = Math.floor(leftTime / 60 / 60 / 24);
									 let h = Math.floor(leftTime  / 60 / 60 % 24);
				                    let m = Math.floor(leftTime  / 60 % 60);
				                     let s = Math.floor(leftTime % 60);
								
									if(d==0){
										return h+""+m+""
									}else{
										return d+"" +h+""+m+""
									}
										
									

				                 }

				                
				                 
			 },sandRed(){
				 if(this.$route.query.id){
					 this.id = this.$route.query.id
				 }
				
			  this.$axios({
			    method: "post",
			    url: "stores/info",
			    data: {},  
			  }).then((res)=>{
			      let data=res.data;
			      let user = JSON.parse(window.localStorage.getItem('user'));
			      if(data.code==200) {
              data.data.store_info.token = user.token
              localStorage.setItem('user', JSON.stringify(data.data.store_info))
              this.user = data.data.store_info

              //,,
              if (this.user.no_pop != 1) {
                Dialog.alert({
                  title: '',
                  message: '4530,,',
                  showCancelButton: true,
                  confirmButtonText: '',
                  cancelButtonText: '',
                }).then(() => {
                  // on confirm
                }).catch(() => {
                  this.nopop()
                });
              }
            }
			   })
			   
			  }, 
		
		sandMate(){
			
			
			
			
			this.news=[]
			this.$axios.get('stores/order_list',{params:{
							  type:this.id,keywords:this.keywords
			}}).then((res)=>{
            // let res={};
            // res.data={"code":200,"message":"\u6210\u529f","data":{"list":{"current_page":1,"data":[{"id":613,"order_no":"D20221207132453531004","max_price":"40.1","min_price":"1","updated_at":"2022-12-07 13:24:55","citys":"\u676d\u5dde","ticket_count":1,"movie_name":"\u5760\u843d","seat_names":"","accept_seats":1,"show_time":1670402400,"close_time":1670408760,"expire_time":1670390875,"cinemas":"\u4e34\u5b89\u6a2a\u5e97\u7535\u5f71\u57ce","halls":"6\u53f7\u6fc0\u5149\u5385(\u5e7f\u544a\u3001\u51a0\u540d\u5385\u62db\u5546)","market_price":"27 x 1","offer_status":0,"offer_times":1,"win_store_id":0,"offer_record_id":2425,"offer_amount":0.01,"store_id":121,"status_txt":"\u7ade\u4ef7\u4e2d","back_times":0,"show_date":"2022-12-07","show_time_txt":"12-07 16:40~18:26"}],"first_page_url":"https:\/\/dyp.ylhwlkj.com\/api\/stores\/order_list?page=1","from":1,"last_page":1,"last_page_url":"https:\/\/dyp.ylhwlkj.com\/api\/stores\/order_list?page=1","next_page_url":null,"path":"https:\/\/dyp.ylhwlkj.com\/api\/stores\/order_list","per_page":20,"prev_page_url":null,"to":1,"total":1}}}
						console.log(res);
            let data=res.data;
						if(data.code==200){
							
						
							if(data.data.list.data.length==0&&this.page==1){
								this.tip=''
								return
							}
							
							if(data.data.list.data.length==0&&this.page!=1){
								this.tip=''
								return
							}
							this.tip=''
							this.news=this.news.concat(data.data.list.data);
							if(this.id==1||this.id==2){
								this.news.forEach(item=>{
									item['hand'] = this.ctime(item.show_time)
								})
								
								setTimeout(this.sandMate, 60000);
								
							}
							
							
						}
			})
			
		
		
		},sandAlt(){
			
				this.news=[]
				this.sandMate()
			
			},wind(id){
			   this.$axios({
			     method: "post",
			     url: "stores/ignore_order",
			     data: {order_no:id.order_no},  
			   }).then((res)=>{
			       let data=res.data;
			      
			       if(data.code==200){
			      Toast(data.message);
			       	this.news=[]
			       	this.sandMate()
			       
			       }else{
					   Toast(data.message);
				   }
			    })
			   
			   }
		}
};
</script>
