<script>
import Title from "@/common/title";
import { Toast } from 'vant';	
export default {
  components: {
    Title,

  },
  data() {
    return {
		reg: {
			id:'',
			market_left:'',
			market_right:'',
			contain_cinema:'',
			un_contain_cinema:'',
			contain_movie:'',
			un_contain_movie:'',
			contain_hall:'',
			un_contain_hall:'',
			lovers_seat:1,
			accept_seats:1,
			seats_number:0,
			offer_type:1,
			offer_value:'',
			state:1
			         
		      },
    
    };
  },mounted() {
	  
	if(this.$route.query.id){
		this.reg.id = this.$route.query.id
		this.funder()
	}
	  
  	
	this.sandMate()
	this.sandCase()
  },methods:{
	   onConfirm(value) {
		  
	       this.reg.areaName = value[0].name+'-'+value[1].name+'-'+value[2].name;
		   this.reg.area_id=value[2].code
		   this.reg.province_name=value[0].name
		   this.reg.city_name=value[1].name
		   this.reg.area_name=value[2].name
	       this.showPicker = false;
	      },
	  submitForm() {
	  				
	  			
	          this.$axios({
	            method: "post",
	            url: "stores/offer_rule",
	            data: this.reg,  
	          }).then((res) => {
	          	let data=res.data;
	          			
	          	if(data.code==200){
	  						
	  			  Toast(data.message);
				  this.$router.back()
	    
	          		
	          	}else{  
	  				Toast(data.message);
	  						      
	  					}
	           
	          });
	  				 
	  		
	       
	       },funder(){
			   this.$axios.get('stores/rule_list',{params:{
				   rule_id:this.reg.id
			   }}).then((res)=>{
				   let data=res.data;
				   if(data.code==200){
					   this.reg=data.data
					   
				   }
			   })
			   

			  				 
	}
	
  }
};
</script>
