<template>
  <div class="login">
	
	<Title v-if="con.offer_id&&con.offer_status==0" titleName='' iconLeft="back"/>
   <Title v-if="!con.offer_id" titleName='' iconLeft="back"/>
   <Title v-if="con.offer_status==1" titleName='' iconLeft="back"/>
<!--    <img style="position: absolute;z-index: 0;width: 100%;height: 5.2rem" src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-dba7ffc8-6102-457c-8941-6212ed989b04/6c557c89-a107-489e-a719-2fb92b0fd379.png" alt="" srcset="">-->
<div class="box1">
	<div class="mem-con">
		<div class="tag" style="background: cornflowerblue" v-if="con.offer_status==1" v-clipboard:copy="con.movie_name+con.show_date+con.show_time+con.close_time+con.cinemas+con.halls+con.seat_names+con.order_no"
							        v-clipboard:success="onCopy">

		</div>
    <div class="tag" style="background: green" v-else-if="con.offer_status==0" v-clipboard:copy="con.movie_name+con.show_date+con.show_time+con.close_time+con.cinemas+con.halls+con.seat_names+con.order_no"
         v-clipboard:success="onCopy">

      </div>
    <div class="tag" style="background: red;color: white" v-else-if="con.offer_status==2&&show" v-clipboard:copy="con.movie_name+con.show_date+con.show_time+con.close_time+con.cinemas+con.halls+con.seat_names+con.order_no"
         v-clipboard:success="onCopy">

      </div>
    <div class="tag" style="background: red;color: white" v-else-if="con.offer_status==2" v-clipboard:copy="con.movie_name+con.show_date+con.show_time+con.close_time+con.cinemas+con.halls+con.seat_names+con.order_no"
         v-clipboard:success="onCopy">

      </div>
    <div class="tag" style="background: #6DAD19" v-else-if="con.offer_status==3" v-clipboard:copy="con.movie_name+con.show_date+con.show_time+con.close_time+con.cinemas+con.halls+con.seat_names+con.order_no"
         v-clipboard:success="onCopy">

      </div>
    <div class="tag" style="background: #6DAD19" v-else-if="con.offer_status==4" v-clipboard:copy="con.movie_name+con.show_date+con.show_time+con.close_time+con.cinemas+con.halls+con.seat_names+con.order_no"
         v-clipboard:success="onCopy">

      </div>
    <div class="heade">
<!--      <img class="img1" :src="con.movie_image" alt="">-->
      <div class="mem-l1">

        <div class="tit">{{con.movie_name}}</div>
        <p>{{con.show_date}} {{con.show_time}}~{{con.close_time}}</p>
        <p>{{con.halls}}</p>
        <p>{{con.seat_names}}</p>
        <p>{{con.cinemas}}</p>
        <p>:{{con.address}}</p>
      </div>
    </div>
    <div class="heade2">
      <p>{{con.order_no}}<div style="margin-left: 10px" v-clipboard:copy="con.order_no"
                                     v-clipboard:success="onCopy">

      </div></p>
    </div>

	</div>
</div>
    <div class="box1">
	<div class="h-act" v-if="con.offer_status==0">
		{{con.offer_timeout}}
	</div>
    </div>
    <div class="box1">
			<div class="card-color">
			
				
			
				<div class="leaf">
					<div class="dont">
						
					</div>
					<div class="fit">
					{{con.seat_names}}
						
					</div>
				</div>
				<div class="leaf">
					<div class="dont">
						
					</div>
					<div class="fit">
				<span class="font-col" v-if="con.seat_flag==0"></span><span class="font-col" v-if="con.seat_flag==1"></span><span class="font-col" v-if="con.seat_flag==2"></span><span class="hand">|</span><span class="font-col" v-if="con.accept_seats==1"></span><span class="font-col" v-if="con.accept_seats==0"></span>
						
					</div>
				</div>
			<div class="leaf">
				<div class="dont">
					<span v-if="con.offer_id">/</span>
				</div>
				<div class="fit">
					<span class="font-col">{{con.market_price}}</span>
				
					<span class="hand">|</span><span class="font-col">{{con.offer_amount}}</span>
				</div>
				
			</div>
		
			<div class="bao" v-if="con.offer_status==0">
				<div class="dont">
					
					<p>{{con.min_price}}-{{con.max_price}}</p>
				</div>
				
				
				<div class="fit">
					<input type="text" v-model="ruleForm.price" placeholder=""/>
				<span>x{{con.ticket_count}}</span>
				</div>
			
			</div>
			<div class="leaf" v-if="!con.offer_id">
				<div class="dont">
					 <img src="../../assets/tis.png"><span class="font-col h-ice">{{m}}{{s}}</span>
				</div>
				
			</div>
			<div class="leaf" v-if="con.offer_status==1">
				<div class="dont">
					
					 <img src="../../assets/tis.png"><span class="font-col h-ice">{{m}}{{s}}</span>
				</div>
			
				
			</div>
			<div class="leaf" v-if="con.offer_status==1">
				<div class="dont">
					
				
				</div>
			
				
			</div>
			</div>
    </div>
<div class="box1">
	<div class="li-give" v-if="con.offer_status==1">
<!--  <div class="li-give">-->
		<div class="tit"><img src="../../assets/noodles.png" @click="runned"></div>
		
		<div class="give">
			
			<div class="user_input">
				
				<div class="lab"> </div><input v-model="ticket_code"  placeholder="" />
				<van-uploader accept="image/*"  :after-read="withOut">
				 <img src="../../assets/pp1.png">
				</van-uploader>
				
			</div>
			<div class="user_input">
				
				<div class="lab"> </div><input v-model="valid_code"  placeholder="()" />
				
			</div>
			<div class="h-car-ed" v-for="(item,index) in stone">
				<div class="rich">{{index+1}}</div>
				<div class="h-bir">
				<div class="ending">
					
					<div class="lab"> </div><input v-model="item.ticket_code"  placeholder="" />
					
				</div>
				<div class="ending">
					
					<div class="lab"> </div><input v-model="item.valid_code"  placeholder="()" />
					
				</div>
				</div>
			</div>
			
			
			<div class="h-late-der">
				<ul>
					<li v-for="(item,index) in sleep" @click="habit(index)">{{item}}
					<img src="../../assets/bg1.png" />
					</li>
					<div class="clear"></div>
				</ul>
			</div>
			
		</div>
	</div>
	<div class="li-give" v-if="con.offer_status==1">
		
		<div class="tit"></div>
		<div class="h-water">
			<p class="font-col h-date"></p>
			
			<van-uploader
			  v-model="uploader_FY"
				:after-read="afterRead"
			  :before-delete="delUploadImg_FY"
			  accept="image/*"
			  multiple
			/>
			<p class="font-col h-date"></p>
		</div>
		
	</div>
	<div class="li-give" v-if="con.offer_status==2&&setshow()">

		<div class="tit">
		<img src="../../assets/noodles.png" @click="runned">
		</div>
		<div class="give">
			
			<div class="user_input">
				
				<div class="lab"> </div><input v-model="ticket_code"  placeholder="" />
				<van-uploader accept="image/*" :after-read="withOut">
				 <img src="../../assets/pp1.png">
				</van-uploader>
			</div>
			<div class="user_input">
				
				<div class="lab"> </div><input v-model="valid_code" type="number"  placeholder="()" />
				
			</div>
			<div class="h-car-ed" v-for="(item,index) in con.ticket_code">
				<div class="rich">{{index+1}}</div>
				<div class="h-bir">
				<div class="ending">
					
					<div class="lab"> </div><input v-model="item.ticket_code"  placeholder="" />
					
				</div>
				<div class="ending">
					
					<div class="lab"> </div><input v-model="item.valid_code"  placeholder="()" />
					
				</div>
				</div>
			</div>
			<div class="li-give" v-if="con.offer_status==2&&setshow()">
			
				<div class="tit"></div>
				<div class="h-water">
					<p class="font-col h-date"></p>
				
						<van-uploader
						  v-model="uploader_FY"
						  :after-read="afterRead"
						  :before-delete="delUploadImg_FY"
						  accept="image/*"
						  multiple
						/>
					<p class="font-col h-date"></p>
					
					
				</div>
				
			</div>
			
		</div>
	</div>
</div>
<div class="box1">
<div v-if="con.offer_status==0" class="h-heal-th">
	
		
		<p class="h-vad-ball"><!--  --></p>
	<div class="hb-button" v-if="con.offer_id" @click="loginForm"></div>
	<div class="hb-button" v-else @click="loginForm"></div>
	
	</div> 
	
	<div class="fast" v-if="con.offer_status==1">
		
		
		<div class="h-pap">
		<div class="h-button ad" v-if="con.seat_status==0" @click="honor"></div>
		</div>
		<div class="h-pap">
			<div class="h-button cloud" @click="ticket"></div>
			
		</div>
		<div class="h-pap">
		<div class="h-button ad" style="background-color: #8684f3" @click="white"></div>
		
		</div>
	</div>
	<div class="fast" v-if="con.offer_status==1">
	
		
	</div>
	<div class="fast" v-if="con.offer_status==2&&setshow()">
	
		<div class="h-button sound" @click="ticket"></div>
		
	
	</div>
	<div class="fast" v-if="con.offer_status>0">
	
	</div>
</div>
<div class="box1">
	<van-dialog v-model="del" @confirm='pick' show-cancel-button>
	 <div class="link">
		 <div class="want">
			 <img v-if="but.images" :src="but.images" />
			 <van-uploader v-else :after-read="afterRead">
			  <van-button round block native-type="submit" >
			   
			  </van-button>
			 </van-uploader>
		 </div>
	
		 <div class="gand">
			 <ul>
				 <li>
					 <input type="text" v-model="but.code" placeholder="()" />
				 </li>
				 <li>
				 	 <input type="text" v-model="but.valid_code" placeholder="()" />
				 </li>
			 </ul>
		 </div>
	 </div>
	</van-dialog>
	<van-dialog v-model="der" title="" @confirm='half'>
		<div class="h-plat">
	   <input type="text" v-model="c1" /> <input type="text" v-model="c2" />
	  </div>
	</van-dialog>
  <van-dialog v-model="chai" title="" @confirm='chaiok'>
    <div class="user_input">
      <input v-model="ticket_code"  placeholder="" /><div class="button1" @click="getcode(chaidata)">|</div>
    </div>
    <div class="user_input">
      <input v-model="valid_code"  placeholder="()" /><div class="button2" @click="getcode2(chaidata)">6</div>
    </div>
  </van-dialog>
</div>
  </div>
</template>
<script>
import Title from "@/common/title";
import Bar from '@/components/bar/bar'
import {Toast,Dialog} from 'vant';	
import QrCode from 'qrcode-decoder'
export default {
  components: {
    Title,
    Bar

  },
  data() {
    return {
      show:false,
    imgurl:'',
		checked:false,
		user:'',
		active: 1,
		id:'',
    chai:false,
    chaidata:{},
		m:0,
		s:0,
		file:'',
		pink:'',
		der:false,
		uploader_FY:[],
		stone:[],
		con:'',
		c1:'',
		c2:'',
		del:false,
		ticket_code:'',
		valid_code:'',
		sleep:[],
		but:{
			order_no:'',
			images:[],
			new_seat_names:'',
			code:[]
		},
		ruleForm:{
			price:'',
			order_no:''
		}
    
    };
  },mounted() {

  	this.id = this.$route.query.id
	this.ruleForm.order_no=this.id
  	this.funder()
    // this.setshow()
// this.sandMate();
  },methods:{
    setshow(){
      //,this.con.show_date+this.con.show_time
      var year = new Date().getFullYear();

      var show_time = this.con.show_date+' '+this.con.show_time+':00';
      console.log(show_time);
      var show_time = new Date(show_time.replace(/-/g, "/")).getTime();
      var now_time = new Date().getTime();
      console.log(show_time,now_time);
      if(show_time<now_time){
        this.show=true
        return false;
      }else{
        // this.show=true
        return true;
      }
    },
	runned(){
		if(!this.ticket_code){
			Toast("") 
			return
		}else{
			let lar={ticket_code:this.ticket_code,valid_code:this.valid_code}
			this.stone.push(lar)
			this.ticket_code=''
			this.valid_code=''
		}
		
	}, onCopy(){
			 Toast("") 
		  },ctime(){
				   
				               
				                 var now = Math.round(new Date().getTime()/1000)
				               
								 
				                var endDate = new Date(this.con.expire_time);
				                 var end = endDate.getTime();
								
				               
				                 var leftTime = end - now;
								
				              
								
				                 if (leftTime >= 0) {
									
				                 
				                     this.m = Math.floor(leftTime / 60 % 60);
				                     this.s = Math.floor(leftTime  % 60);
									
				                 }
								 
				              
				                
				                 setTimeout(this.ctime, 1000);
			 },afterRead(file) {
		
		 this.$axios({
		   method: "post",
		   url: "stores/upload",
		   data: {file:file.content}
		 }).then((res) => {
	
		 	let data=res.data;
		 	if(data.code==200){
			
		 		this.but.images.push(data.data.img)
		 	}
		  
		 });
		 
	
	    },
    chaiok(){
      this.chai=false
    },
		withOut(file) {
		 // let url = window.webkitURL.createObjectURL(file.file)
     //  console.log(file)
		 // let qrr = new QrCode()
		 // qrr.decodeFromImage(url).then(val => {
     //   console.log(val)
			//  let fond=val.data.split("|")
     //   if (fond.length == 2) {
     //     this.ticket_code = fond[0]
     //     this.valid_code = fond[1]
     //   } else {
     //     this.ticket_code = fond[0]
     //   }
			//
		 //  });
     //  this.chai = true
      this.$axios({
        method: "post",
        url: "stores/upload",
        data: {file:file.content}
      }).then((res) => {
        let data=res.data;
        if(data.code==200){
          this.decode(data.data.img)
        }

      });
		},
    decode(img){
      this.$axios({
        method: "get",
        url: "https://www.devtool.top/api/qrcode/decode/remote?url="+img,
      }).then((res1) => {

        this.chaidata=res1
        this.getcode(res1)
        console.log(this.chaidata)
        this.chai=true
      });
    },
    getcode(res){
      let data = res.data.data.text;
      console.log(data)
      let fond=data.split("|")
      console.log(fond)
      if (fond.length == 2) {
        this.ticket_code = fond[0]
        this.valid_code = fond[1]
      } else {
        this.ticket_code = fond[0]
      }
    },
    getcode2(res){
      let data = res.data.data.text;
      let data1 = data.substring(data.length-6,data.length)
      this.valid_code = data1
      this.ticket_code = data.substring(0,data.length-6)
    },
		habit(fell){
			this.der=true
			this.pink=fell
		},
		ticket(){
			let but=this.but
			
			but.order_no=this.id
			
			but.images=but.images.join(',')
			
			but.new_seat_names=this.sleep.join(',')
			
			if(this.ticket_code){
				let lar={ticket_code:this.ticket_code,valid_code:this.valid_code}
				this.stone.push(lar)
			}
			
			if(this.con.ticket_code.length>0){
				but.code=JSON.stringify(this.con.ticket_code)
			
			}else{
				but.code=JSON.stringify(this.stone)
				
			}
				
			
			
			this.$axios({
			      method: "post",
			      url: "stores/out_ticket_one",
			      data: but
			    }).then((res) => {
			    	let data=res.data;
			    	
			    	if(data.code==200){
								
					this.$router.push({ path: "/user/comment" });	
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
					this.but.images=[]
								}
			     
			    });
				
				
				
				
			
			},
			half(){
				this.der=false
				this.sleep[this.pink]=this.c1+''+this.c2+''
			},
			pick(){
				
			this.but.order_no=this.id
				
			this.$axios({
			      method: "post",
			      url: "stores/create_ticket",
			      data: this.but
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==200){
									
					this.funder()
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},loginForm(){
				
				if(this.ruleForm.price == ''){
				
					Toast('');
					return
				}
				
				
			this.$axios({
			      method: "post",
			      url: "stores/create_offer",
			      data: this.ruleForm,  
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==200){
									
					this.$router.back()
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},white(){
			
				Dialog.confirm({
				  message:
				    '',
				})
				  .then(() => {
				    this.$axios({
				          method: "post",
				          url: "stores/backout",
				          data: {order_no:this.id}
				        }).then((res) => {
				        	let data=res.data;
				        			
				        	if(data.code==200){
				    						
				    		this.$router.back()
				    			Toast(data.message);
				    		
				        		
				        	}else{  
				    		Toast(data.message);
				    						      
				    					}
				         
				        });
				    	
				  })
				

				
				
			
				
				
				
			
			},fight(){
			
				
			this.$axios({
			      method: "post",
			      url: "stores/cancel_offer",
			      data: {offer_id:this.con.offer_id}
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==200){
									
					this.$router.back()
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},honor(){
			
				
			this.$axios({
			      method: "post",
			      url: "stores/release-seat",
			      data: {order_no:this.id}
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==200){
									
				this.funder()
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},funder(){
			
			   this.$axios.get('stores/order_info',{params:{
				  order_no:this.id
			   }}).then((res)=>{
          // let res={}
          //  res.data = {"code":200,"message":"","data":{"order_no":"CK20221126135741531005","ticket_count":1,"movie_name":"\u6cbc\u6cfd\u6df1\u5904\u7684\u5973\u5b69","show_time":"17:29","close_time":"19:29","cinema_id":3173024,"cinemas":"\u4e0a\u6d77\u5e02 \u00b7 \u5510\u5fb7\u56fd\u9645\u5f71\u57ce\u2014\u4e0a\u6d77\u7f57\u6cfe\u5e97","amount":45,"buyer_phone":"","accept_seats":1,"seat_flag":"0","market_price":45,"halls":"\u4e94\u53f7\u5385\uff08\u7981\u6b62\u996e\u98df\uff09","seat_names":"7\u63926\u5ea7","expire_time":1669444248,"offer_status":2,"show_version":"\u56fd\u8bed 2D","store_id":121,"movie_image":"https:\/\/gw.alicdn.com\/tfscom\/i4\/O1CN016sRRcY1KNf44THzzC_!!6000000001152-0-alipicbeacon.jpg_320x320.jpg","address":"\u5b9d\u5c71\u533a\u7f57\u6cfe\u9547\u8427\u6708\u8def7-11\uff08\u5355\uff093\u5c42\u533a\u5510\u5fb7\u56fd\u9645\u5f71\u57ce\u7f57\u6cfe\u5e97","offer_amount":0.1,"offer_id":2378,"show_date":"11-30","min_price":"1","max_price":40.5,"seat_status":0,"ticket_timeout":30,"offer_timeout":3}}
				   let data=res.data;
				   if(data.code==200){
					
					   this.con=data.data
					   this.ctime()
					   this.but.new_seat_names=this.con.seat_names
					   this.sleep=this.but.new_seat_names.split(',')
					   this.stone=this.con.ticket_code
					   this.con.images=this.con.images
					   if(this.con.images){
						    this.but.images=this.con.images
					   }
					  
						if(this.con.offer_status==2){
							this.con.images.forEach(item=>{
													   this.uploader_FY.push({url:item})
							})
						}
					  
					   this.ruleForm.order_no=this.id
             console.log(res);
					   
				   }
			   })
			   

			  				 
	}
  }
};
</script>
<style lang="scss" scoped>
.button1{
  padding: 0.08rem 0.2rem;
  background: blue;
  border-radius: 0.4rem;
  color: white;
  width: 1.4rem;
  text-align: center;
}
.button2{
  padding: 0.08rem 0.2rem;
  background: red;
  border-radius: 0.4rem;
  color: white;
  width: 1.4rem;
  text-align: center;
}
	.heade{
	  display: flex;
	  // align-items: center;
	  width: 100%;
	}
	.heade2{
    display: flex;
	  color: #ffffff;
	  width: 100%;
	}
//.box1{
//  padding: 10px 20px 0px 20px;
//  position: relative;
//}
