<script>
import Title from "@/common/title";
import { Toast } from 'vant';	
export default {
  components: {
    Title
  },
  data() {
    return {
      type:"1",
		title:'',
		taking_mode:false,
		channel_id:0,
		images:[],
		info:'',
		user:'',
		weight:'',
		page:1,
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:''

    };
  },  mounted() {
 this.channel_id = this.$route.query.id;
 this.sandMate();
this.seat()
	this.sandMate();
		
		
		},methods: {
    selecttype(type) {
      if(type=="2"){
        window.location.href = "https://dyp.ylhwlkj.com/#/user/exc";
        // window.location.href = "https://xcx.ylhwlkj.com/#/user/exc";
      }
    },
			mode() {
				
	          this.$axios({
	            method: "post",
	            url: "stores/mode",
	            data: {taking_mode:this.taking_mode}
	          }).then((res) => {
	          	let data=res.data;
	          			
	          	if(data.code==200){
	  						
	  			  Toast(data.message);
				
	    
	          		
	          	}else{  
	  			 Toast(data.message);
	  						      
	  			}
	           
	          });
	  				 
	  				 
	         
	       
	       },
			  seat(){
			  	this.user = JSON.parse(window.localStorage.getItem('user'));
			  
			  },sandAlt(){
				this.news=[]
				this.sandMate()
			
			}, blue(){
				
				
			this.$axios({
			      method: "post",
			      url: "stores/apply-withdraw",
			      data:{}
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==200){
									
					
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},sandMate(){
		
		this.$axios({
		  method: "post",
		  url: "stores/info",
		  data: {},  
		}).then((res)=>{
		    let data=res.data;
		    let user = JSON.parse(window.localStorage.getItem('user'));
		    if(data.code==200){
		    	data.data.store_info.token=user.token
		    	localStorage.setItem('user',JSON.stringify(data.data.store_info))
		    	this.user=data.data.store_info
				if(data.data.store_info.store_info.taking_mode==1){
					this.user.store_info.taking_mode=false
				}else{
					this.user.store_info.taking_mode=true
				}
		    	
		    
		    }
		 })
	
	}
		}
};
</script>
