<script>
import Title from "@/common/title";
import { Toast } from 'vant';	
export default {
  components: {
    Title
  },
  data() {
    return {
      timedai:null,
      dainum:'',
      type:"1",
		title:'',
		id:3,
		keywords:'',
		images:[],
		info:'',
		user:'',
		page:1,
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:''

    };
  },  mounted() {
	
	// this.showtop();
	this.sandRed()
	this.sandMate()
	this.getdainum()
		
		
		},methods: {
    getdainum(){
      this.$axios.get('stores/order_list',{params:{
          type:3,keywords:''
        }}).then((res)=>{
        console.log(res);
        let data=res.data;
        if(data.code==200){
          if(data.data.list.data.length!=0){
            this.dainum = data.data.list.data.length
            this.timedai=data.data.list.data[0].expire_time
            console.log(this.timedai)
            let a = new Date().getTime()
            this.timedai = this.timedai*1000-a
            console.log(this.timedai)
            // this.timedai = this.timedai/1000
            // console.log(this.timedai)
          }
        }
      })
    },
    baojia(it){
      if(it.number == null){

        Toast('');
        return
      }


      this.$axios({
        method: "post",
        url: "stores/create_offer",
        data: {
          price:it.number,
          order_no:it.order_no
        },
      }).then((res) => {
        let data=res.data;

        if(data.code==200){

          this.sandMate()
          Toast(data.message);


        }else{
          Toast(data.message);

        }

      });
    },
    selecttype(type) {
      if(type=="2"){
        window.location.href = "https://dyp.ylhwlkj.com";
        // window.location.href = "https://xcx.ylhwlkj.com";
      }



    },
    showtop(){
      //
      this.$dialog.alert({
        title: '',
        message: ''
      }).then(() => {
        // on close
      });
    },
			ctime(id){
			
				
				 					
				 								 var now= Math.round(new Date().getTime()/1000).toString();
				 								
				        
				             var leftTime =  id-now ;
				          
				 								 
				             if (leftTime >= 0) {
				 									 
				               let d = Math.floor(leftTime / 1000 / 60 / 60 / 24);
				 				let h = Math.floor(leftTime / 60 / 60 % 24);
				                let m = Math.floor(leftTime  / 60 % 60);
				                 let s = Math.floor(leftTime % 60);
				 									
				 								
				 				return h+""+m+""
				 									
				             }
							
				                
				                 
			 },sandRed(){
				 if(this.$route.query.id){
					 this.id = this.$route.query.id
				 }
				
			  this.$axios({
			    method: "post",
			    url: "stores/info",
			    data: {},  
			  }).then((res)=>{
			      let data=res.data;
			      let user = JSON.parse(window.localStorage.getItem('user'));
			      if(data.code==200){
			      	data.data.store_info.token=user.token
			      	localStorage.setItem('user',JSON.stringify(data.data.store_info))
			      	this.user=data.data.store_info
			      	
			      
			      }
			   })
			   
			  }, 
		
		sandMate(){
			
			
		
			this.news=[]
			this.$axios.get('stores/order_list',{params:{
							  type:this.id,keywords:this.keywords
			}}).then((res)=>{
						let data=res.data;
						if(data.code==200){
							
						
							if(data.data.list.data.length==0&&this.page==1){
								this.tip=''
								return
							}
							
							if(data.data.list.data.length==0&&this.page!=1){
								this.tip=''
								return
							}
							this.tip=''
							this.news=this.news.concat(data.data.list.data);
							
								this.news.forEach(item=>{
									item['hand'] = this.ctime(item.show_time)
								})
								
								setTimeout(this.sandMate, 60000);
								
							
							
							
						}
			})
			
		
		
		},sandAlt(){
			
				this.news=[]
				this.sandMate()
			
			},wind(id){
			   this.$axios({
			     method: "post",
			     url: "stores/ignore_order",
			     data: {order_no:id.order_no},  
			   }).then((res)=>{
			       let data=res.data;
			      
			       if(data.code==200){
			      Toast(data.message);
			       	this.news=[]
			       	this.sandMate()
			       
			       }else{
					   Toast(data.message);
				   }
			    })
			   
			   }
		}
};
</script>
